ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

img {
  display: block;
}

.search-form {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: teal;
  padding: 10px 0;
  display: flex;
  align-items: center;
  justify-content: center;

}

.search-input {
  box-shadow: 0 5px 5px #777;
  height: 20px;
}

.search-btn {
  background-color: white;
  height: 26px;
  width: 26px;
  padding: 0;
  border-color: white;
  box-shadow: 0 5px 5px #777;
}

.gallery {
  margin-top: 30px;
  width: 1200px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 10px;
  padding: 20px 20px;
  margin-left: auto;
  margin-right: auto;
}

.photo-img {
  border-radius: 5px;
  width: 100%;
  height: 170px;
  object-fit: cover;
}

.photo-card {
  background-color: rgb(251, 248, 248);
  display: block;
  padding: 10px;
  width: 270px;
  box-shadow: 0 15px 10px #777;
}

.info {
  line-height: 0, 8;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

.load-more {
  display: block;
  margin-left: auto;
  margin-right: auto;
  height: 26px;
  box-shadow: 0 5px 5px #777;
}

.info-item {
  text-align: center;
}